// common saga to handle activity indicator, error handler etc.
import { put } from 'redux-saga/effects';

export function* fetchStart() {
  yield put({ type: 'FETCH_STARTED' });
}
export function* fetchEnd() {
  yield put({ type: 'FETCH_ENDED' });
}

export function* handleError(err) {
  yield put({ type: 'ERROR', err });
}



