// @flow
// Globally shared states should be placed here such as activity indicator, erorr, token, authorization etc; you get the point :)

// Constants
export const FETCH_STARTED = 'FETCH_STARTED';
export const FETCH_ENDED = 'FETCH_ENDED';

// Action creators
const initialState = {
  isLoading: 0,
  error: false,
  errorMessage: '',
  token: ''
};

const app = (state = initialState, action = {}) => {
  switch (action.type) {
    case FETCH_STARTED:
      return { ...state, isLoading: state.isLoading + 1 };
    case FETCH_ENDED:
      return {
        ...state,
        isLoading: state.isLoading - 1 < 0 ? 0 : state.isLoading - 1
      };
    default:
      return state;
  }
};

export default app;



