import { put, call } from 'redux-saga/effects';
import fetch from 'isomorphic-fetch';
import urls from '../configs';
import { fetchStart, fetchEnd, handleError } from './common';

function fetchTokenApi(action) {
  console.log(action);

  // const url = urls.tokenService;
  const url = urls.workStation;
  return fetch(url, {
    method: 'get',
    credentials: 'include'
  })
    .then(handleError)
    .then(response => response.json());
}

export function* fetchToken(action) {
  yield call(fetchStart);
  try {
    const response = yield call(fetchTokenApi, action);
    if (response.data) {
      yield put({
        type: 'FETCH_TOKEN_SUCCEEDED',
        data: response.data
      });
    } else {
      yield put({
        type: 'FETCH_TOKEN_FAILED'
      });
    }
  } catch (err) {
    yield put({ type: 'FETCH_TOKEN_FAILED' });
  }
  yield call(fetchEnd);
}

export function testing() {
  console.log('testing saga');
}



