import React from 'react';
import { Row, Layout, Icon } from 'antd';
// header styles
import './style.scss';
// header component
const AppliationHeader = () => {
  const { Header } = Layout;
  return (
    <Header className="page-header">
      <Row>
        <img src="/assets/images/spine.png" width="50" alt="e-vertebrates" />
        <h1>
          eVertebrates
          <span>An Annotated checklist of vertebrates species in nepal                              </span>
        </h1>
      </Row>
    </Header>
  );
};

export default AppliationHeader;



