import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Switch, Route } from 'react-router-dom';
import { Layout, Spin } from 'antd';
// ant design uses less :(
import 'antd/lib/style/index.less';
// over-ride styles for our project, yup its not best so to future => TODO: search for better option.
import '../overRideAntdLessStyles.scss';
import '../style.scss';
import ApplicationHeader from '../../Common/Header';
import ApplicationMenu from '../../Common/Menu';
import Home from '../../Home';
import AboutUs from '../../AboutUs';

class AppComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: 'home' // eslint-disable-line
    };
  }
  render() {
    return (
      <Spin tip="Loading..." spinning={this.props.isLoading > 0}>
        <Layout className="page">
          <ApplicationHeader />
          {/* <ApplicationMenu /> */}
          <Layout className="page-content">
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/aboutus" component={AboutUs} />
            </Switch>
          </Layout>
        </Layout>
      </Spin>
    );
  }
}

AppComponent.propTypes = {
  isLoading: PropTypes.number.isRequired
};

export default AppComponent;



