import React, { Component } from 'react';
import { Layout, Select, Icon, Input, AutoComplete, Breadcrumb } from 'antd';
import autoBind from 'auto-bind';
import vertebratesData from '../../../evertebrates';

class HomeComponent extends Component {
  static populateSelect(data) {
    const { Option } = Select;
    return data.map(d => (
      <Option key={d} value={d}>
        {d}
      </Option>
    ));
  }

  constructor(props) {
    super(props);
    this.state = {
      vertebrates: {}, // eslint-disable-line
      phylumList: [],
      selectedPhylum: '',
      klassList: [],
      selectedKlass: '',
      familyList: [],
      selectedFamily: '',
      genusList: [],
      selectedGenus: '',
      speciesList: [],
      selectedSpecies: '',
      dataSource: [],
      filterDataSource: [],
      selectedGenusSpecies: {}
    };
    autoBind(this);
  }
  componentDidMount() {
    this.extractData(vertebratesData);
  }
  onPhylumChange(selectedPhylum) {
    this.setState({ selectedPhylum });
  }
  onKlassChange(selectedKlass) {
    this.setState({ selectedKlass });
  }
  onFamilyChange(selectedFamily) {
    this.setState({ selectedFamily });
  }
  onGenusChange(selectedGenus) {
    this.setState({ selectedGenus });
  }
  onSpeciesChange(selectedSpecies) {
    this.setState({ selectedSpecies });
  }
  onSelect(value) {
    const selectedGenusSpecies = vertebratesData.find(
      data => data.Genus_species === value
    );
    this.setState({ selectedGenusSpecies });
  }
  handleSearch(value) {
    this.setState({
      filterDataSource: this.state.dataSource.filter(
        data => data.indexOf(value) > -1
      )
    });
  }
  extractData(data) {
    const {
      phylumList,
      klassList,
      familyList,
      genusList,
      speciesList,
      dataSource
    } = this.state;
    data.forEach(species => {
      if (phylumList.indexOf(species.Phylum) === -1) {
        phylumList.push(species.Phylum);
      }
      if (klassList.indexOf(species.Class) === -1) {
        klassList.push(species.Class);
      }
      if (familyList.indexOf(species.Family) === -1) {
        familyList.push(species.Family);
      }
      if (genusList.indexOf(species.Genus) === -1) {
        genusList.push(species.Genus);
      }
      if (speciesList.indexOf(species.Species) === -1) {
        speciesList.push(species.Species);
      }
      dataSource.push(species.Genus_species);
    });

    this.setState({
      phylumList,
      klassList,
      familyList,
      genusList,
      speciesList,
      dataSource,
      filterDataSource: dataSource
    });
  }
  showSearchResult(value) {
    console.log(this.state.selectedGenusSpecies, value);
  }
  render() {
    const {
      selectedPhylum,
      selectedKlass,
      selectedFamily,
      selectedGenus,
      selectedSpecies,
      filterDataSource,
      selectedGenusSpecies
    } = this.state;

    const { Sider, Content } = Layout;
    return (
      <Layout className="page">
        <Sider
          className="sidebar"
          theme="light"
          style={{ background: '#fff', padding: 20, width: 300 }}
        >
          <h3> Taxonomy filter </h3>
          <div className="form-element">
            <label>Phylum</label>
            <Select
              showSearch={false}
              style={{ width: '100%' }}
              placeholder="Select phylum"
              onChange={this.onPhylumChange}
            >
              {HomeComponent.populateSelect(this.state.phylumList)}
            </Select>
          </div>
          <div className="form-element">
            <label>Class</label>
            <Select
              showSearch={false}
              style={{ width: '100%' }}
              placeholder="Select class"
              disabled={!selectedPhylum}
              onChange={this.onKlassChange}
            >
              {HomeComponent.populateSelect(this.state.klassList)}
            </Select>
          </div>
          <div className="form-element">
            <label>Family</label>
            <Select
              showSearch={false}
              style={{ width: '100%' }}
              placeholder="Select a person"
              optionFilterProp="children"
              disabled={!selectedPhylum || !selectedKlass}
              onChange={this.onFamilyChange}
            >
              {HomeComponent.populateSelect(this.state.familyList)}
            </Select>
          </div>
          <div className="form-element">
            <label>Genus</label>
            <Select
              showSearch={false}
              style={{ width: '100%' }}
              placeholder="Select genus"
              disabled={!selectedPhylum || !selectedKlass || !selectedFamily}
              onChange={this.onGenusChange}
            >
              {HomeComponent.populateSelect(this.state.genusList)}
            </Select>
          </div>
          <div className="form-element">
            <label>Species</label>
            <Select
              showSearch={false}
              style={{ width: '100%' }}
              placeholder="Select species"
              disabled={
                !selectedPhylum ||
                !selectedKlass ||
                !selectedFamily ||
                !selectedGenus
              }
              onChange={this.onGenusChange}
            >
              {HomeComponent.populateSelect(this.state.speciesList)}
            </Select>
          </div>
        </Sider>
        <Content className="content">
          <h2> Search</h2>
          <div className="form-species">
            <label> Search by </label>
            <AutoComplete
              dataSource={filterDataSource}
              style={{ width: 200 }}
              onSelect={this.onSelect}
              onSearch={this.handleSearch}
              placeholder="Genus species"
              onChange={this.showSearchResult}
            >
              <Input
                suffix={
                  <Icon type="search" className="certain-category-icon" />
                }
              />
            </AutoComplete>
          </div>
          {Object.keys(selectedGenusSpecies).length > 0 && (
            <div>
              <div className="breadcurmb">
                <Breadcrumb>
                  <Breadcrumb.Item>
                    {selectedGenusSpecies.Phylum}
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    {selectedGenusSpecies.Class}
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    {selectedGenusSpecies.Family}
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    {selectedGenusSpecies.Genus}
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    {selectedGenusSpecies.Species}
                  </Breadcrumb.Item>
                </Breadcrumb>
              </div>
              <div className="genus-species-details">
                <h3>
                  Genus Species:{' '}
                  <span> {selectedGenusSpecies.Genus_species}</span>
                </h3>
                <p>
                  Author : <span> {selectedGenusSpecies.Author}</span>
                </p>
                <p>
                  Taxonomic searial no. :
                  <span> {selectedGenusSpecies['Taxonomic Serial No']}</span>
                </p>
                <p>
                  Common Name :{' '}
                  <span> {selectedGenusSpecies['Common names']}</span>
                </p>
                <p>
                  Remark : <span> {selectedGenusSpecies.Remark}</span>
                </p>
                <p>
                  Endemism : <span> {selectedGenusSpecies.Endemism}</span>
                </p>
                <p>
                  Source : <span> {selectedGenusSpecies.Source}</span>
                </p>
                <table>
                  <thead>
                    <tr>
                      <th>IUCN REdlist CAtegory </th>
                      <th> CITES Appendex</th>
                      <th> Nepal protechtion status</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{selectedGenusSpecies['IUCN Redlist Category']}</td>
                      <td>{selectedGenusSpecies['CITES Appendix']}</td>
                      <td>{selectedGenusSpecies['Nepal Protection status']}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </Content>
      </Layout>
    );
  }
}

export default HomeComponent;



