// importing required libraries
import 'babel-polyfill';
import React from 'react';
import { render } from 'react-dom';
import { createStore, applyMiddleware, compose } from 'redux';
import createSagaMiddleware from 'redux-saga';
// redux logger for debugging purpose
import { createLogger } from 'redux-logger';
// react hot reloading HOC
import { AppContainer } from 'react-hot-loader';
// importing root app
import App from './containers/Root';
// service worker register/unregister handler
import registerServiceWorker from './registerServiceWorker';
// importing root saga
import rootSaga from './sagas';
// importing root reducer
import rootReducer from './reducers';

// Specify extensions options like name, actionsBlacklist, actionsCreators, serialize...
const composeEnhancers =
  typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({})
    : compose;
// saga middleware
const sagaMiddleware = createSagaMiddleware();
// redux logger middleware
const logger = createLogger();
// middlewares list for this app
let middleWares = [sagaMiddleware];
// adding development only middleWares to app middleware
if (process.env.NODE_ENV !== 'production') {
  middleWares = [...middleWares, logger];
}
// wrapping app middleware in dev tool enhancer for debugging purpose for development locally
const enhancer =
  process.env.NODE_ENV !== 'production'
    ? composeEnhancers(applyMiddleware(...middleWares))
    : applyMiddleware(...middleWares);
// get the server generated state if we use server-side rendering in future
const preloadedState = window.__PRELOADED_STATE__;
// allow the passed state to be garbage-collected as its purpose is met above
delete window.__PRELOADED_STATE__;
// mount it on the Store
const store = createStore(rootReducer, enhancer, preloadedState);
// give rootSaga to saga middleware and run it
sagaMiddleware.run(rootSaga);
//  render function
const renderer = Root =>
  render(
    <AppContainer>
      <Root store={store} />
    </AppContainer>,
    document.getElementById('app')
  );

// initial render
renderer(App);

// hot reloading for development
if (module.hot) {
  // Enable Webpack hot module replacement for components
  module.hot.accept();
  // Enable Webpack hot module replacement for reducers
  module.hot.accept('./reducers', () => {
    const nextRootReducer = require('./reducers/index');
    store.replaceReducer(nextRootReducer);
  });
}

// handle service worker
registerServiceWorker();



